:root {

  --heading-font: 'Special Elite', sans-serif;
  --main-font: 'Raleway';
  --alt-font: 'Nixie One';
}

* {
  padding: 0px;
  margin: 0px;
  font-family: var(--main-font), sans-serif;
}

a {
  text-decoration: none;
}

.app {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  background-image: url('./pic/lightpaperfibers.png');
  background-repeat: repeat;
}

.hide {
  display: none;
}

#container {
  flex: 1;
}

.headerDiv {
  padding-top: 20vh;
}

.heading {
  font-family: var(--heading-font);
  text-align: center;
  font-size: 3em;
  padding-left: 20px;
  padding-right: 20px;
}

/* NAVBAR */

#navbar {
  width: auto;
  width: 800px;
  margin-left: auto;
  margin-right: auto;
}

li {
  list-style: none;
}

#nav-ul {
  display: flex;
  justify-content: space-between; 
  align-content: stretch;
}

.nav-button {
  padding-top: 25px;
  padding-bottom: 25px;
  width: 200px;
  text-align: center;
  background-size: contain;
  background-repeat: no-repeat;
  font-size: 1.5em;
  transition: 0.4s ease-in-out;
}

.nav-button a {
  font-family: var(--heading-font);
  color: white;
}

.nav-button:first-child {
  background-image: url('./pic/dry-brush-stroke.png');
}

.nav-button:nth-child(2) {
  background-image: url('./pic/dry-brush-stroke-2.png');
}

.nav-button:nth-child(3) {
  background-image: url('./pic/dry-brush-stroke-38.png');
}

.nav-button:last-child {
  background-image: url('./pic/dry-brush-stroke-17.png');
}

/* HAMBURGER MENU */

#hamburger-menu {
  display: none;
}

/* HOME PAGE */
.home-tagline {
  padding-top: 50px;
  padding-right: 25px;
  padding-left: 25px;
  text-align: center;
  font-size: 1.5em;
}

/* ABOUT PAGE */

#about {
  max-width: 1000px;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
}

#about-summary {
  padding: 20px 20px 10px 20px;
  margin: 0 30px 50px 30px;
  border: 1px solid black;
}

.profile-img {
  max-width: 300px;
  float: left;
  padding-right: 25px;
}

#about-heading {
  padding-bottom: 50px;
}

.about-text {
  font-size: 1.1em;
  text-align: justify;
  padding-bottom: 15px;
}

/* PORTFOLIO */

#portfolio {
  max-width: 1000px;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
}

#portfolio-heading {
  padding-bottom: 25px;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.card {
  width: 400px;
  height: 330px;
  margin-top: 50px;
  margin-left: 3%;
  margin-right: 3%;
  border: 1px solid black;
}

.card-image {
  width: 100%;
}

.card-link {
  text-decoration: none;
}

.card-title {
  text-align: center;
  padding-top: 25px;
  font-size: 1.2em;
  color: black;
}

.card:last-child {
  margin-bottom: 50px;
}

/* ACADEMIC CV */

#cv {
  max-width: 1000px;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
}

#cv-heading {
  padding-bottom: 25px;
}

.cv-text {
  text-align: left;
  font-size: 1.2em;
  padding: 15px;
}

/* FOOTER */

#footer {
  padding-top: 20px;
  padding-bottom: 20px;
}

#footer-div-line {
  max-width: 300px;
  height: 1px;
  background-color: black;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 30px;
}

#contact-ul {
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: space-between;
  padding-right: 20px;
  padding-left: 20px;
}

#copyright {
  font-size: 0.8em;
  text-align: center;
  margin-right: auto;
  margin-left: auto;
  padding-top: 30px;
  padding-right: 15px;
  padding-left: 15px;
}

/* MEDIA QUERIES */

@media screen and (max-width: 915px) {
  .card {
    margin-right: auto;
    margin-left: auto;
  }
}

@media screen and (max-width: 800px) {
  #navbar {
    display: none;
  }

  .hamburger-menu {
    display: flex;
    flex-direction: column;
    position: fixed;
    width: 40px;
    height: 40px;
    margin-top: 10px;
    margin-left: 10px;
    border-radius: 3px;
    justify-content: space-evenly;
  }

  .hamburger-stripe {
    height: 4px;
    width: 35px;
    margin-left: auto;
    margin-right: auto;
    background: black;
    border-radius: 2px;
  }
  
  .headerDiv {
    padding-top: 15vh;
  }

  .mobile-menu {
    position: fixed;
    width: 100%;
  }

  #nav-ul {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding-bottom: 20px;
  }

  #nav-escape {
    position: fixed;
    width: 40px;
    height: 40px;
    margin-top: 10px;
    margin-left: 10px;
    color: white;
    font-size: 3em;
  }

  .menu-nav-button {
    background: black;
    width: 100%;
    color: white;
    padding-bottom: 20px;
    font-size: 2em;
  }

  .menu-nav-button:first-child {
    padding-top: 20px;
  }

  .menu-nav-button a {
    color: white;
  }

  .profile-img {
    margin-left: auto;
    margin-right: auto;
    display: block;
    float: none;
    padding-right: 0;
    padding-bottom: 25px;
  }

  .about-text {
    padding-right: 15px;
    padding-left: 15px;
  }

  .heading {
    font-size: 2em;
  }

  .hide {
    display: none;
  }
}
